import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/index/Index'
import Guide from '@/guide/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/guide',
      name: 'guide',
      component: Guide,
    }
  ]
})



