<template>
  <div id="index">
    <img id="title" src="../assets/top_bg_mobile_ratio.png"/>
    <div class="logo_bg">
      <img class="app_icon" src="https://appicon.pgyer.com/image/view/app_icons/af2a856cf6e48aaa8775a83b12ab9c6b/120"/>
    </div>
    <div>
      <div class="app_name">Alpha-{{clientUA}}</div>
    </div>

    <div class="qr_code">
      <img class="img_qr_code" v-bind:src="qrCode"/>
    </div>
    <btn-guide v-if="isIOS"/>

    <div class="install">
      <a v-bind:href="downloadUrl">
        点击安装
      </a>
    </div>

  </div>
</template>
<script>
  import BtnGuide from '../component/BtnGuide'


  export default {
    name: 'Index',
    data () {
      return {
        qrCode: '',
        downloadUrl: "https://www.pgyer.com/apiv2/app/install?_api_key=845b1e8faab9233cd759c6d3c9fe69ce&buildKey=5687b65a54188b7d1ff27f2512f1427b",
        clientUA: "Android",
        isIOS: false
      }
    },
    components: {BtnGuide},
    mounted () {
      this.checkUA();
    },
    methods: {
      checkUA () {
        let ua = navigator.userAgent.toLowerCase();
        //Android终端
        let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;
        //Ios终端
        let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        if (isWeixinBrowser()) {
          this.$router.push({
            path: '/product'
          })
        } else {
          if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
            //Ios
            this.downloadUrl = "https://testflight.apple.com/join/2hecYXyZ";
            this.qrCode = require('../assets/ios.png');
            this.clientUA = "IOS"
            this.isIOS = false;
          } else if (/(Android)/i.test(navigator.userAgent)) {
            //Android终端
            this.qrCode = require('../assets/android.png')
          }
        }

        function isWeixinBrowser () {
          return (/micromessenger/.test(ua)) ? true : false;
        }
      }
    }
    ,
    component: {
      'btn_guide':
      BtnGuide
    }
  }
</script>

<style scoped>
  #title {
    width: 100%;
  }

  .logo_bg {
    background: url('https://static.pgyer.com/static/images/template/colorful/app_bg.png') no-repeat scroll top center;
    width: 160px;
    height: 160px;
    background-size: 100% auto;
    margin: auto;
    text-align: center;
  }

  #index {
    width: 100%;
    position: relative;
  }

  div {
    display: block;
  }

  .app_name {
    font-size: 1rem;
    text-align: center;
  }

  .logo_bg .app_icon {
    border: none;
    width: 100px;
    height: 100px;
    margin-top: 34px;
  }

  .qr_code {
    margin-top: 10px;
    width: min-content;
    height: min-content;
    margin-left: auto;
    margin-right: auto;
    border: darkgray 1px solid;
  }

  .qr_code .img_qr_code {
    width: 160px;
    height: 160px;
    vertical-align: bottom;
  }

  .install {
    text-align: center;
    margin: 40px auto auto auto;
  }

  a {
    position: relative;
    border-radius: 30px;
    background: #1abc9c;
    text-decoration: none;
    color: white;
    padding: 10px 30px 10px 30px;
  }
</style>



